<style lang="scss" rel="stylesheet/scss">
  .result {

    .result-text {
      margin-left: 20px;
    }
  }
</style>

<template>

  <img :src="browser">
  <div class="table-responsive-vertical shadow-z-1">
    <table class="table">
      <tbody>
      <tr v-for="result in results">
        <td><img :src="result.browser | browserImage" alt="">
          <span class="blue">{{result.browser |  capitalize}}</span>
        </td>
        <td>{{result.text }}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script type="text/babel">
  export default {
    name: 'results',

    computed: {
      browser(){

        // return require('../assets/images/' + this.result.browser + '.png')
      }
    },
    props: {
      results: {
        required: true,
        type: Array
      }
    },
    filters: {
      browserImage(image) {
        return require('../assets/images/' + image + '.png')
      },
      capitalize(text) {

        return text.charAt(0).toUpperCase() + text.slice(1);
      }
    }
  }

</script>




