<style lang="scss" rel="stylesheet/scss">
  .key {
    font-size: 36px;
    font-weight: 900;
    display: flex;
    align-items: center;
    .inner-key {

      padding: 15px;
      background: #f5f5f5;
      position: relative;
      &:hover {
        .remove {
          display: block;
        }
      }
      .remove {
        outline: none;
        display: none;
        cursor: pointer;
        background: none;
        border: none;
        position: absolute;
        top: -12px;
        right: -12px;
        // transform: translateX(-50%)

      }

    }
    .cross {
      margin: 0 15px;
    }

  }
</style>

<template>


  <div class="key">
    <span v-if="index > 0" class="cross">+</span>
    <div class="inner-key">
      {{key.name}}

      <button class="remove" @click="remove = key">
        <svg fill="#f54034" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg">
          <path d="M0 0h24v24H0z" fill="none"/>
          <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm5 11H7v-2h10v2z"/>
        </svg>
      </button>
    </div>
  </div>

</template>

<script type="text/babel">

  import getKeyByCharCode from '../services/hotkeys'

  export default {


    props: {
      remove: {

        required: false,
        default: false,

      },
      key: {
        type: Object,
        required: true,
        //   twoWay: true
      },
      index: {
        type: Number,
        required: false
      }
    }
  }

</script>




