
const browserData = require('../data/shortcuts.json')
export default function (searchString) {

  var results = []

  for (var browser in browserData) {
    var currentBrowser = browserData[ browser ].name;
    browserData[ browser ].shortcuts.find((shortcut) => {
      if (shortcut.key.toLowerCase() === searchString.toLowerCase()) {
        results.push({
          text: shortcut.text,
          browser: currentBrowser
        })
        return true;
      }
    })
  }
  return results;

}






